import React from 'react'

const ProgressBar = ({ percentage, progress, ...rest }) => {
  const classProgress = progress === 'Error' ? 'progress-bar progress-bar-striped bg-danger' : (
    percentage < 100 ? 'progress-bar progress-bar-striped bg-primary' :
      'progress-bar bg-success'
  );

  return (
    <div className='progress' {...rest}>
      <div
      className={classProgress}
      role='progressbar'
      style={{ width: percentage + '%' }}
      aria-valuenow={percentage}
      aria-valuemin='0'
      aria-valuemax='100'
    >
      {percentage}%
    </div>
  </div>
  )
}
export default ProgressBar;