import React from 'react';
import Button from './Button';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFileDownload, faTimes } from '@fortawesome/free-solid-svg-icons';
import { formatBytes } from '../../utils/string.util';
import ItemDownload from '../common/ItemDownload';

const DownloadList = ({ list, onRemove, onRemoveAll }) => {
  return (
    <>
      <div className='d-flex mb-3'>
        <h5 className='flex-grow-1' style={{ display: 'inline-block' }}>
          Result Files:
        </h5>
        <Button
          text='Clear All'
          color='outline-success'
          size='btn-sm'
          onClick={onRemoveAll}
        />
      </div>
      <ul className='list-group'>
        {list.map((file) => (
          <li key={file.linkDownload} className='list-group-item'>
            <div className='d-flex justify-content-between w-100 mb-2'>
              <span className='flex-grow-1' style={{ wordBreak: 'break-all' }}>
                <FontAwesomeIcon
                  icon={faFileDownload}
                  style={{ marginRight: '10px' }}
                />
                {file.fileName} - {formatBytes(file.size)}
              </span>
              <span className='item-close ml-3'>
                {' '}
                <FontAwesomeIcon
                  icon={faTimes}
                  onClick={onRemove(file)}
                  style={{ marginLeft: '10px' }}
                />
              </span>
            </div>
            <ItemDownload name={file.fileName} url={file.linkDownload} />
          </li>
        ))}
      </ul>
    </>
  );
};

export default DownloadList;
