const Button = ({
  text,
  color = 'primary',
  size = '',
  isLoading = false,
  ...rest
}) => {
  if (size) size = ' ' + size;
  const classBtn = `btn btn-${color}${size}`;
  return (
    <>
      {isLoading ? (
        <button className={classBtn} style={{ marginRight: '10px' }} type='button' disabled>
          <span
            className='spinner-border spinner-border-sm'
            role='status'
            aria-hidden='true'
          ></span>
          {' '}Loading...
        </button>
      ) : (
        <button type='button' className={classBtn} {...rest}>
          {text}
        </button>
      )}
    </>
  );
};

export default Button;
