import React, { useRef } from 'react';
import { useSpring, animated } from 'react-spring';
import InputFile from './InputFile';
import Clipboard from './Clipboard';

const Input = ({
  label,
  name,
  type = 'text',
  error,
  nameFile,
  onGetFile,
  isHasCopy = false,
  ...rest
}) => {
  const styles = useSpring({ opacity: error ? 1 : 0 });
  const txt = useRef(null);

  return (
    <>
      <div className='mb-3'>
        <label htmlFor={name} className='form-label'>
          {label}:{' '}
          {onGetFile && <InputFile nameFile={nameFile} onGetFile={onGetFile} />}
        </label>
        {isHasCopy && <Clipboard refElement={txt} />}
        <input
          type={type}
          className={error ? 'form-control is-invalid' : 'form-control'}
          name={name}
          id={name}
          ref={txt}
          {...rest}
        />
        {error && (
          <animated.div style={styles} className='invalid-feedback d-block'>
            {error}
          </animated.div>
        )}
      </div>
    </>
  );
};
export default Input;
