import React, { useState } from 'react';
import Button from '../common/Button';
import Input from '../common/Input';
import Select from '../common/Select';
import Textarea from '../common/Textarea';
import { toast } from 'react-toastify';
import { generateKeyPair } from '../../services/asymmetric.service';
import { validateProperty, validateForm } from '../common/Form';
import { KeyPairSchema } from '../../validations/asymmetric.schema';
const KeyGenerator = () => {
  const listOptions = [
    {
      id: 1024,
      name: 1024,
    },
    {
      id: 2048,
      name: 2048,
    },
    {
      id: 3072,
      name: 3072,
    },
    {
      id: 4096,
      name: 4096,
    },
    {
      id: 5120,
      name: 5120,
    },
    {
      id: 6144,
      name: 6144,
    },
    {
      id: 7168,
      name: 7168,
    },
  ];
  const [keyValues, setKeyValues] = useState({
    bitSize: 0,
    pass: 'secret',
  });

  const [keyGenerare, setKeyGenerare] = useState({
    publicKey: '',
    privateKey: '',
  });
  const [statusLoading, setStatusLoading] = useState(false);

  const [errors, setErrors] = useState({});

  const handleGenerator = async () => {
    const errors = validateForm(KeyPairSchema, keyValues);
    setErrors(errors || {});
    if (errors) return;
    try {
      setStatusLoading(true);
      const values = { ...keyValues };
      values.passphrase = values.pass;
      delete values.pass;
      const response = await generateKeyPair(values);
      const { publicKey, privateKey } = response.data;
      const data = { ...keyGenerare };
      data.publicKey = publicKey;
      data.privateKey = privateKey;
      setKeyGenerare(data);
    } catch (error) {
      const { message } = error.response.data;
      toast.error(message);
    } finally {
      setStatusLoading(false);
    }
  };

  const handleChange = ({ currentTarget: input }) => {
    const errorsData = { ...errors };
    const error = validateProperty(KeyPairSchema, input);
    if (error) errorsData[input.name] = error;
    else delete errorsData[input.name];
    setErrors(errorsData);

    const data = { ...keyValues };
    data[input.name] =
      input.name === 'bitSize' ? Number(input.value) : input.value;
    setKeyValues(data);
  };

  return (
    <div className='card'>
      <div className='card-header'>KEY GENERATOR</div>
      <div className='card-body'>
        <div className='container'>
          <Select
            label='Key Size'
            name='bitSize'
            options={listOptions}
            error={errors['bitSize']}
            onChange={handleChange}
          />
          <Input
            label='Passpharse (default: secret)'
            name='pass'
            error={errors['pass']}
            value={keyValues.pass}
            onChange={handleChange}
          />
          <div className='mb-3'>
            <Button
              color='outline-primary'
              text='Generate'
              isLoading={statusLoading}
              onClick={handleGenerator}
            />
          </div>
          <Textarea
            label='Public Key'
            name='publicKey'
            rows='4'
            readOnly
            isHasCopy={keyGenerare.publicKey !== ''}
            value={keyGenerare.publicKey}
          />
          <Textarea
            label='Private Key'
            name='privateKey'
            rows='4'
            readOnly
            isHasCopy={keyGenerare.privateKey !== ''}
            value={keyGenerare.privateKey}
          />
        </div>
      </div>
    </div>
  );
};

export default KeyGenerator;
