import { React, useState, useCallback, useEffect } from 'react';
import { useDropzone } from 'react-dropzone';
import Button from './Button';
import ProgressBar from './ProgressBar';
import { useSpring, useTransition, animated, config } from 'react-spring';
import { formatBytes } from '../../utils/string.util';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faCloudUploadAlt,
  faFileUpload,
  faTimes,
} from '@fortawesome/free-solid-svg-icons';

const FileUpload = ({ error, percentage, progress, onGetFiles }) => {
  const [listFiles, setListFiles] = useState([]);
  const styleError = useSpring({
    opacity: error ? 1 : 0,
    config: config.stiff,
  });
  const styleListFiles = useSpring({
    opacity: listFiles.length > 0 ? 1 : 0,
    config: config.stiff,
  });
  const styleProgressBar = useSpring({
    opacity: percentage > 0 ? 1 : 0,
    config: config.stiff,
  });
  const listTransitions = useTransition(listFiles, {
    config: config.gentle,
    from: { opacity: 0, height: 0, transform: 'translate3d(-25%, 0px, 0px)' },
    enter: { opacity: 1, height: 45, transform: 'translate3d(0%, 0px, 0px)' },
    leave: { opacity: 0, height: 0, transform: 'translate3d(25%, 0px, 0px)' },
    keys: listFiles.map((file) => file.path),
  });
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const onDrop = useCallback((acceptedFiles) => {
    setListFiles([...acceptedFiles]);
  });

  const { getRootProps, getInputProps } = useDropzone({
    onDrop,
  });

  useEffect(() => {
    onGetFiles(listFiles);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [listFiles]);

  const handleRemove = (file) => () => {
    const newFiles = [...listFiles];
    newFiles.splice(newFiles.indexOf(file), 1);
    setListFiles(newFiles);
  };

  const handleRemoveAll = () => {
    setListFiles([]);
  };

  const files = (
    <ul className='list-group'>
      {listTransitions((styles, file) => (
        <animated.li style={styles} className='list-group-item d-inline-flex'>
          <span className='flex-grow-1' style={{ wordBreak: 'break-all' }}>
            <FontAwesomeIcon
              icon={faFileUpload}
              style={{ marginRight: '10px' }}
            />
            {file.path} - {formatBytes(file.size)}
          </span>
          <span className='item-close ml-3'>
            {' '}
            <FontAwesomeIcon
              icon={faTimes}
              onClick={handleRemove(file)}
              style={{ marginLeft: '10px' }}
            />
          </span>
        </animated.li>
      ))}
    </ul>
  );

  const formatNameFiles = (list) => (list.length > 1 ? 'Files' : 'File');

  return (
    <>
      <div {...getRootProps({ className: 'dropzone mb-3' })}>
        <div className='dz-message d-flex flex-column'>
          <input {...getInputProps()} />
          <span style={{ textAlign: 'center' }}>
            <FontAwesomeIcon icon={faCloudUploadAlt} size='5x' />
          </span>
          Drag &amp; Drop here or click
        </div>
      </div>
      {(error && listFiles.length === 0) && (
        <animated.div style={styleError} className='invalid-feedback d-block'>
          {error}
        </animated.div>
      )}
      {listFiles.length > 0 && (
        <animated.div style={styleListFiles} className='list-files'>
          <div className='d-flex'>
            <h5 className='flex-grow-1' style={{ display: 'inline-block' }}>
              {formatNameFiles(listFiles)}
              <small>
                ({listFiles.length} file{listFiles.length > 1 && 's'})
              </small>
              {percentage > 0 && (
                <p style={{ fontSize: '16px' }}>
                  Progress:{' '}
                  <span
                    className={
                      progress === 'Error'
                        ? 'ml-3 text-danger'
                        : 'ml-3 mr-3 text-success'
                    }
                  >
                    {progress}{' '}
                    {percentage < 100 && (
                      <span
                        className='spinner-border spinner-border-sm text-success'
                        role='status'
                      >
                        <span className='visually-hidden'>Loading...</span>
                      </span>
                    )}
                  </span>
                </p>
              )}
            </h5>
            <div>
              <Button
                text='Clear All'
                color='outline-success btn-sm btn-block'
                size='btn-sm'
                onClick={handleRemoveAll}
              />
            </div>
          </div>
          <animated.div style={styleProgressBar} className='mb-3'>
            {percentage > 0 && (
              <ProgressBar percentage={percentage} progress={progress} />
            )}
          </animated.div>
          <aside className='mb-3'>{files}</aside>
        </animated.div>
      )}
    </>
  );
};

export default FileUpload;
