import { useSpring, animated } from 'react-spring';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faExclamationTriangle } from '@fortawesome/free-solid-svg-icons';

const Select = ({ label, name, status, options, error, ...rest }) => {
  const styles = useSpring({ opacity: error ? 1 : 0 });
  const statusIcon =
    status && status === 'loading' ? (
      <span
        className='spinner-border spinner-border-sm text-success d-inline-block'
        style={{ marginLeft: '12px' }}
        role='status'
      >
        <span className='visually-hidden'>Loading...</span>
      </span>
    ) : status && status === 'error' ? (
      <span className='d-inline-block ml-5'>
        <FontAwesomeIcon
          icon={faExclamationTriangle}
          style={{ marginLeft: '10px', color: 'red' }}
        />
      </span>
    ) : (
      ' '
    );

  return (
    <div className='mb-3'>
      {label && (
        <label htmlFor={name} className='form-label'>
          {label}:{statusIcon}
        </label>
      )}
      <select
        name={name}
        id={name}
        className={error ? 'form-select is-invalid' : 'form-select'}
        {...rest}
      >
        <option value=''>Select</option>
        {options.map((option) => (
          <option key={option.id} value={option.id}>
            {option.name}
          </option>
        ))}
      </select>
      {error && (
        <animated.div style={styles} className='invalid-feedback d-block'>
          {error}
        </animated.div>
      )}
    </div>
  );
};

export default Select;
