import http from './base-http.service';

const uploadFilesSymmetric = (url, payload, onUploadProgress) => {
  const formData = new FormData();
  payload.files.forEach((file, index) => formData.append(`files[${index}]`, file));
  formData.append('algorithm', payload.algorithm);
  formData.append('key', payload.key);
  formData.append('iv', payload.iv);
  formData.append('isHexKey', payload.isHexKey);
  formData.append('isHexIv', payload.isHexIv);
  return http.post(url, formData, {
    headers: {
      'Content-Type': 'multipart/form-data',
    },
    onUploadProgress
  });
};

