const downloadAllFile = (urls) => {
  const request = urls.map((url) => downloadFile(url));
  return http.all(request);
};

const downloadFile = (url, onDownloadProgress) => {
  return http.get(url, {
    responseType: 'blob',
    onDownloadProgress,
  });
};
export { uploadFilesSymmetric, uploadFilesAsymmetric, uploadFilesAdvanced, downloadFile, downloadAllFile };
