import React, { useRef } from 'react';
import { useSpring, animated } from 'react-spring';
import InputFile from './InputFile';
import Clipboard from './Clipboard';

const Textarea = ({
  label,
  name,
  error,
  rows = 3,
  onGetFile,
  isHasCopy = false,
  ...rest
}) => {
  const styles = useSpring({ opacity: error ? 1 : 0 });
  const txa = useRef(null);

  return (
    <>
      <div className='mb-3'>
        <label htmlFor={name} className='form-label'>
          {label}: {onGetFile && <InputFile onGetFile={onGetFile} />}
        </label>
        {isHasCopy && <Clipboard refElement={txa} />}
        <textarea
          className={error ? 'form-control is-invalid' : 'form-control'}
          name={name}
          id={name}
          rows={rows}
          ref={txa}
          {...rest}
        ></textarea>
        {error && (
          <animated.div style={styles} className='invalid-feedback d-block'>
            {error}
          </animated.div>
        )}
      </div>
    </>
  );
};
export default Textarea;
