import React from 'react';
import Button from './Button';

function RadioButton({ name, radios, isEnableGen, onGenerate, ...rest }) {
  return (
    <div className='d-flex justify-content-between mb-3'>
      <span className='text-left'>
        {radios.map((radio) => (
          <div key={radio.id} className='form-check form-check-inline'>
            <input
              className='form-check-input'
              type='radio'
              name={name}
              id={radio.id}
              value={radio.checked}
              defaultChecked={radio.checked}
              {...rest}
            />
            <label className='form-check-label' htmlFor={radio.id}>
              {radio.text}
            </label>
          </div>
        ))}
      </span>

      {onGenerate && (
        <span className='text-right'>
          <Button
            color='outline-primary btn-sm'
            onClick={onGenerate}
            text='Generate Random'
            disabled={!isEnableGen}
          />
        </span>
      )}
    </div>
  );
}

export default RadioButton;
