import http from './base-http.service';
import { uploadFilesAsymmetric } from './file.service';
import config from '../config/default.json';

const apiEndpoint = config.api + '/asymmetric';

const generateKeyPair = (payload) => {
  return http.post(apiEndpoint + '/genenrate-key-pair', payload);
};

const getKeyValue = (file) => {
  return http.getKeyValue(file);
};

const encryptText = (payload) => {
  return http.post(apiEndpoint + '/encrypt/text', payload);
};

const decryptText = (payload) => {
  return http.post(apiEndpoint + '/decrypt/text', payload);
};

const encryptFile = (payload, onUploadProgress) => {
  return uploadFilesAsymmetric(
    apiEndpoint + '/encrypt/files',
    payload,
    onUploadProgress,
  );
};

const decryptFile = (payload, onUploadProgress) => {
  return uploadFilesAsymmetric(
    apiEndpoint + '/decrypt/files',
    payload,
    onUploadProgress,
  );
};

export {
  getKeyValue,
  generateKeyPair,
  encryptText,
  decryptText,
  encryptFile,
  decryptFile,
};
