import React, { useState } from 'react';
import { toast } from 'react-toastify';
import { downloadFile } from '../../services/file.service';
import Button from './Button';
import ProgressBar from './ProgressBar';

const ItemDownload = ({ name, url }) => {
  const [percentage, setPercentage] = useState(0);
  const [progress, setProgress] = useState('Download');

  const handleDownload = () => {
    setPercentage(0);
    downloadFile(url, (event) => {
      setPercentage(Math.round((100 * event.loaded) / event.total));
    })
      .then((response) => {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement('a');
        link.href = url;
        link.setAttribute('download', name);
        document.body.appendChild(link);
        link.click();
        setProgress('Done');
      })
      .catch((err) => {
        setProgress('Error');
        toast.error(err);
      });
  };

  const classBtn =
    progress === 'Error'
      ? 'danger btn-sm'
      : progress === 'Done'
      ? 'success btn-sm'
      : 'outline-success btn-sm';
  return (
    <div className='d-flex justify-content-between align-items-center'>
      <div className='flex-grow-1'>
        <ProgressBar
          percentage={percentage}
          progress={progress}
          style={{ width: 96 + '%' }}
        />
      </div>
      <div>
        <Button text={progress} color={classBtn} onClick={handleDownload} disabled={progress === 'Done'} />
      </div>
    </div>
  );
};
export default ItemDownload;
